/* stylelint-disable */
/* Media Query Mixins */
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-size: 100%;
}

body {
  background-color: #fef3f8;
  line-height: 1.5;
  color: #404041;
}

a {
  display: inline-block;
  text-decoration: none;
}

textarea {
  resize: none;
}

h1 {
  color: #fff;
  font-family: "Montseratt", sans-serif;
  font-size: calc(
		30px + 20 * ((100vw - 320px) / 1120)
	);
  line-height: 1.1;
}

h2,
h3,
h4 {
  text-align: center;
}

p,
a {
  font-family: "Open Sans", sans-serif;
}

.container {
  max-width: 120rem;
  width: 90%;
  margin: 0 auto;
}

.heading {
  font-size: 3rem;
  font-family: "Montseratt", sans-serif;
  font-weight: 500;
}

.underline {
  width: 5rem;
  height: 0.3rem;
  margin: 0 auto 1rem;
  background-color: #ed1e79;
}

.lang-active {
  background: #fff;
  border-radius: 0.5rem;
}

.btn {
  color: #fff;
  background-color: #ed1e79;
  padding: 1rem 2rem;
  border-radius: 0.5rem;
  transition: all 0.3s ease-out;
  font-size: calc(
		15px + 5 * ((100vw - 320px) / 1120)
	);
}
.btn:hover {
  background-color: #c81061;
}

/* Scroll Top Button */
.top-btn {
  position: fixed;
  bottom: 2rem;
  right: 2rem;
  padding: 8px;
  cursor: pointer;
  width: 3rem;
  height: 3rem;
  background: #ed1e79;
  color: #fff;
  border: none;
  outline: none;
  opacity: 0.8;
  border-radius: 0.5rem;
}
.top-btn svg {
  fill: #fff;
  width: 100%;
  height: 100%;
}

.dis-scroll {
  overflow: hidden;
  height: 100vh;
}

/* stylelint-disable */
/* Media Query Mixins */
.menu {
  display: flex;
  padding: 0 4rem;
  height: 6rem;
  position: fixed;
  inset: 0;
  z-index: 3;
  background-color: #fef3f8;
  align-items: center;
  justify-content: space-between;
}
.menu__logo {
  display: inherit;
  align-items: center;
  column-gap: 5px;
}
.menu__logo img {
  width: 3rem;
  height: 3rem;
}
.menu__logo-text {
  font-family: "Pacifico", cursive;
  font-size: calc(
		20px + 10 * ((100vw - 320px) / 1120)
	);
  color: #ed1e79;
}
.menu__contact-info {
  display: inherit;
  margin-top: 0.5rem;
  justify-content: center;
  width: 60%;
}
.menu .contact {
  display: inherit;
}
.menu .contact:nth-child(n+2) {
  margin-left: 3rem;
}
.menu .contact__icon {
  width: 1.2rem;
  height: 1.2rem;
  fill: #ed1e79;
  align-self: center;
}
.menu .contact p,
.menu .contact a {
  font-size: calc(
		12px + 3 * ((100vw - 320px) / 1120)
	);
  padding-left: 0.5rem;
  align-self: center;
  font-weight: 600;
}
.menu .contact a {
  text-decoration: none;
  color: inherit;
}
.menu__lang {
  margin-top: 0.5rem;
}
.menu__lang a {
  font-size: calc(
		12px + 3 * ((100vw - 320px) / 1120)
	);
  padding: 0.5rem;
  font-weight: 600;
  color: #ed1e79;
}
.menu__btn {
  margin-top: 1rem;
  cursor: pointer;
}
.menu__btn .btn-line {
  width: 28px;
  height: 3px;
  margin: 0 0 5px 0;
  background: #000;
  transition: all 0.3s ease-out;
  border-radius: 0.5rem;
}
.menu__btn.close .btn-line:nth-child(1) {
  transform: rotate(45deg) translate(6px, 6px);
}
.menu__btn.close .btn-line:nth-child(2) {
  opacity: 0;
}
.menu__btn.close .btn-line:nth-child(3) {
  transform: rotate(-45deg) translate(5px, -6px);
}
@media screen and (max-width: 1025px) {
  .menu {
    padding: 0 1rem;
  }
  .menu__contact-info {
    width: 57%;
  }
  .menu .contact:nth-child(n+2) {
    margin-left: 1rem;
  }
}
@media screen and (max-width: 820px) {
  .menu__contact-info {
    width: auto;
  }
  .menu__contact-info .contact:nth-child(1),
  .menu__contact-info .contact:nth-child(3) {
    display: none;
  }
}
@media screen and (max-width: 460px) {
  .menu__logo-text {
    display: none;
  }
}

.nav-menu {
  position: fixed;
  padding: 2rem 0;
  width: 100%;
  height: auto;
  opacity: 0.9;
  top: 6rem;
  z-index: 2;
  text-align: center;
  font-family: "Montseratt", sans-serif;
  background-color: #fef3f8;
  transform: translateY(-150%);
  transition: all 0.3s ease-out;
}
.nav-menu ul {
  line-height: 2.5;
}
.nav-menu a {
  font-size: calc(
		20px + 5 * ((100vw - 320px) / 1120)
	);
  color: #ed1e79;
}
.nav-menu a:hover::after {
  width: 100%;
}
.nav-menu a::after {
  content: "";
  display: block;
  width: 0%;
  height: 2px;
  background: #ed1e79;
  transition: all 0.3s ease-out;
  transform: translateY(-1rem);
}
.nav-menu .active::after {
  width: 100%;
}

.active-menu {
  transform: translateY(0);
}

