  }
}
@media screen and (max-width: 1025px) {
  .contacts {
    display: grid;
    grid-template-columns: 1fr;
  }
  .contacts .contacts-info {
    width: 100%;
    padding: 5rem 5rem;
  }
  .contacts .contacts-info form .form-group {
    margin-bottom: 3rem;
  }
  .contacts .map {
    width: 100%;
    height: 40rem;
  }
}

